.seller-homepage {
    padding: 1%;
    text-align: center;
    width: 100vw;
    min-height: 100vh;
    background: #fff;
}

.add-new{
    position: absolute;
    top: 20px;
    right: 120px;
}

.log-out {
    position: absolute;
    top: 20px;
    right: 20px;
}

.product-list {
    display: flex;
    flex-wrap: wrap;
    justify-content: flex-start;
    gap: 20px;
    margin-top: 20px;
    padding: 0 6%;
}

.product-seller {
    height: fit-content;
    padding: 15px;
    width: 265px;
}

.product-image img {
    max-width: 100%;
    max-height: 200px;
    object-fit: cover;
    margin-bottom: 10px;
}

.product-details h2 {
    font-size: 18px;
    margin: 10px 0;
}

.product-details p {
    margin: 5px 0;
}
.product-image{
    margin: 10px 0;
}
.product-image a{
    margin: 0 5px;
}
.seller-button {
    background-color: #007bff;
    color: #fff;
    border: 1px solid #007bff;
    padding: 10px 20px;
    border-radius: 5px;
    cursor: pointer;
    margin: 10px;
}
.danger{
    border: 1px solid #ff2b2b;
    color: #ff2b2b;
    background: white;
}
.danger:hover{
    background: #ff2b2b;
    color: white;
}

.add-new:hover {
    background-color: #0056b3;
    border: 1px solid #0056b3;
}
