@import url('https://fonts.googleapis.com/css2?family=Didact+Gothic&family=Syne:wght@400;600&display=swap');

* {
    margin: 0;
    padding: 0;
}

.create-product {
    max-width: 600px;
    margin: 0 auto;
    padding: 20px;
    background-color: #f8f8f8;
    border-radius: 5px;
    box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.2);
    margin-top: 10px;
    margin-bottom: 10px;
    font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
}

.small-img {
    height: 100px;
}

.img-with-btn {
    display: flex;
    flex-direction: column;
    margin: 8px 1px;
}


.create-product h2 {
    margin-bottom: 10px;
}

.form-group {
    margin-bottom: 15px;
}

.label-createproduct {
    display: block;
    font-weight: bold;
}

.input-createproduct{
    width: 100%;
    padding: 10px;
    border: 1px solid #ccc;
    border-radius: 5px;
}

.create-product-button {
    background-color: #007bff;
    color: #fff;
    border: none;
    border-radius: 5px;
    padding: 10px 20px;
    cursor: pointer;
}

.images-glare {
    display: flex;
    width: 100%;
    flex-wrap: wrap;
    align-items: center;
    justify-content: space-between;
}

.remove-btn {
    border: 1px solid #ff2b2b;
    color: #ff2b2b;
    background: white;
    margin-top: 20px;
}


.remove-btn:hover {
    background: #ff2b2b;
    color: white;
}

.create-product-button:disabled {
    background-color: #ccc;
    color: #666;
    cursor: not-allowed;
    border: 1px solid #ccc;
    opacity: 0.7;
}

.cancel-btn {
    width: 100%;
    border-radius: 5px;
    padding: 10px 20px;
    margin-top: 10px;
    font-size: 16px;
}

@media (max-width: 768px) {
    .create-product {
        max-width: 100%;
        padding: 10px;
    }
}